* {
  margin: 0;
  padding: 0;
}
.app {
  height: 100vh;
  width: 100%;
  gap: 10px;
  max-width: 1499px; /* Adjust the width as needed */
  /* margin: 0 auto; */
  /* overflow-x: hidden; */
  height: auto;
  display: flex;
  padding: 10px 10px 10px 10px;
  background-color: black;
}

.Profile {
  width: 30%;
  height: 100%;
  /* flex: 1;
  flex-wrap: wrap; */
  background-color: #1e1e1f;
  box-shadow: rgba(0, 0, 0, 0.25);
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  line-height: 1.65;
  color: white;
  border: 1px solid #383838;
  border-radius: 20px;
}
.toggle-button {
  display: none;
}
@media (max-width: 1460px) {
  .toggle-button {
    display: block;
    font-size: 15px;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 10px;
    padding: 20 20;
    color: #ffdb6e;
    border-radius: 10 10;
    background: linear-gradient(135deg, #404040, rgba(64, 64, 64, 0) 48.44%);
  }

  .info-laptop {
    display: none; /* Hide information on mobile */
  }

  .icon-text {
    display: none; /* Hide the div by default */
  }

  .icon-text.open {
    display: block; /* Show the div when open */
  }
}
.profile-image {
  overflow: hidden;
  margin: 50 50 0 50px;
  height: auto;
  border-radius: 20px;
}
.employee-info {
  flex: 1;
}
.employee-name {
  overflow: hidden;
  font-size: 22px;
  padding: 10px;
  margin: 0 4rem;
  border-radius: 8px;
}
.profile {
  padding: 0.5rem 1rem;
  border-radius: 8px;
  background: #2a2a2b;
  align-items: center;
  margin: 0 5rem;
  /* margin-left: 30px; */
}
/* .profile {
  margin-left: 5rem;
  padding: 7px 12px;
} */

.separator {
  width: 80%;
  height: 1px;
  margin: 20;
  background: black;
}

.separator2 {
  width: 100%;
  height: 1px;
  margin: 20 0;
}
@media (max-width: 1460px) {
  .Profile {
    width: 100%;
    height: 100%;
    flex-direction: column;
  }
  .image {
    max-width: auto;
  }
  .profile {
    margin-left: 30px;
  }
  .separator {
    width: 95%;
  }
}

/* email,phone no. */
.contact-info {
  padding: 10px;
  /* display: flex; */
  flex-direction: column;
  gap: 10px;
}
.icon-box {
  width: 30px;
  height: 30px;
  margin-bottom: 20px;
  border: 1px solid lightgrey;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.25);
  justify-content: center;
  font-size: 1rem;
  display: inline-flex;
  align-items: center;
}

.overhead {
  padding-left: 50px;
  display: flex;
  font-size: 12px;
  color: #d6d6d6;
}

.overhead-info {
  padding: 15px;
  color: #fff;
}
.socials-link {
  height: 10px;
  padding: 10px;
  margin: 20px;
}
.socials-link i {
  font-size: 25px;
  color: #d6d6d6;
}
.socials-link a {
  padding: 20px;
  margin: 20px;
}

@media (max-width: 1460px) {
  .employee-info > h1,
  .profile {
    width: 200px;
    display: flex;
    flex-direction: column;
  }
  .image {
    max-width: 200px;
    height: auto;
  }
}

.profile-information {
  width: 100%;
  height: 100%;
  background-color: #1e1e1f;
  box-shadow: rgba(0, 0, 0, 0.25);
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  line-height: 1.65;
  color: red;
  border: 1px solid #383838;
  border-radius: 20px;
}
@media (max-width: 1460px) {
  .app {
    flex-direction: column;
  }
  .profile.profile-information {
    width: 100%;
    height: 100%;
  }
}

.container {
  width: 80%;
  height: 100%;
  padding: 40px;
  flex-grow: 1;
  background-color: #1e1e1f;
  box-shadow: rgba(0, 0, 0, 0.25);
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  line-height: 1.65;
  color: black;
  border-radius: 20px;
}

.navbar {
  background-color: #2a2a2b;
  color: #d6d6d6;
  height: 30px;
  float: right;
  padding: 20px 30px;
  border: 1px solid #383838;
  background: #333;
  border-radius: 0 20 0 20;
}

