.logo {
  font-size: 1.5em;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin: 0 10px;
  cursor: pointer;
}

.nav-links li:hover {
  color: #ffbc5e;
}

.hamburger {
  display: none;
  font-size: 1.5em;
  cursor: pointer;
}

@media (max-width: 1108px) {
  .nav-links {
    display: none;
    flex-direction: column;
  }

  .nav-links.active {
    display: flex;
  }
  .nav-links li {
    /* margin: 0 10px; */
    cursor: pointer;
  }

  .hamburger {
    display: block;
  }
}

/* Haeaders */
.head {
  color: white;
  font-size: 2rem;
  position: relative;
}
.head > h1 {
  color: white;
  font-size: 2rem;
  /* padding-top: 60px; */
  position: relative;
}
/* Paragraph */
.paragraph {
  padding: 20 0 20 0;
  font-size: 1rem;
  color: #d6d6d6;
}

/* Pofrtfolio card */
.card {
  width: 400px;
  height: 100px;
  padding: 16px;

  /* border: 1px solid; */
  border-radius: 8px;
  color: white;
  background-color: #2a2a2b;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.card:hover {
  transform: scale(1.05);
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 10px;
  margin-bottom: 10px;
}
.card-name {
  font-size: 1.5em;
}
.card-about {
  font-size: 1em;
  color: #d6d6d6;
}

@media screen and (max-width: 972px) {
  .card {
    width: 100%;
    height: 100%;
  }
}
/* Page-carousel */
.carousel {
  height: 100px;
  /* max-width: 35rem; */
  display: flex;
  flex-wrap: nowrap;
  /* justify-content: space-around; */
}
.carousel > img {
  margin: 10px;
  height: auto;
  min-width: 150px;
  filter: grayscale(100%);
  border: 1px solid black;
}
.carousel > img:hover {
  filter: grayscale(0%);
}
@media screen and (max-width: 1108px) {
  .carousel {
    width: 100%;
    height: 100%;
  }
  .card {
    width: 100%;
    height: 100%;
  }
}
/* .university-about {
  margin: 10px;
} */

@media screen and (max-width: 397px) {
  .app {
    padding: 10px;
  }
  .card {
    width: 20%;
    height: 100%;
  }
}

/* Education section */

.subject-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
}
.subject {
  min-width: 80px;
  text-align: center;
  flex: 1;
  margin: 5px;
  border-radius: 10px;
  background-color: black;
  color: white;
  padding: 6px 15px;
  background-color: rgb(48, 45, 45);
  /* background: linear-gradient(135deg, #0f0e0e, rgba(10, 10, 10, 0) 45.44%); */
  box-shadow: 0 16px 30px rgba(0, 0, 0, 0.25);
}

.subject-container .subject {
  font-size: 10px;
}
.subject-container .wsubject {
  font-size: 10px;
}

.university-container {
  margin: 20px;
  color: #d6d6d6;
  line-height: 1.65;
  font-size: 15px;
  padding: 10px;
  font-family: "Poppins", sans-serif;
}
.university {
  display: flex;
  align-items: center;
  margin-top: 10px;

  justify-content: space-between;
}
.university-item > h6 {
  margin-top: 10px;
  font-size: 15px;
  color: white;
  font-family: "Poppins", sans-serif;
}

.university-location {
  /* margin-top: 5px; */
  display: flex;
}

.project-container {
  column-count: 2;
  margin: 15px;
}
/*Work section*/
.workList {
  padding-bottom: 10px;
  padding-left: 30px;
}

/*Skills section*/

.skillContainer {
  display: flex;
  flex-wrap: wrap;
}
.skill-box {
  background-color: #f9f9f9;
  margin: 20px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-width: 20px;
  max-width: 400px;
  border-radius: 20px;
  background-color: #404040;

  /* box-shadow: 0 16px 30px rgba(0, 0, 0, 0.25); */
}
.specification {
  margin-top: 10px;
  font-size: 20px;
  color: white;
  font-family: "Poppins", sans-serif;
}
