@media screen and (max-width: 972px) {
  .skillsContainer {
    flex-wrap: wrap;
  }
}
/*contact Form*/
.contact-form {
  background-color: #1e1e1f;
  color: white;
  margin-top: 20px;
  border-radius: 8px;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
}
.namae {
  display: flex;
}
.contactsubject {
  flex: 1;
}
.contact-form h2 {
  margin-bottom: 10px;
}

.contact-form div {
  margin-bottom: 10px;
}

.contact-form label {
  display: block;
  margin-bottom: 10px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  background-color: #1e1e1f;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.contact-form button {
  padding: 1rem 1.5rem;
  transition: all 0.4s ease-in-out;
  color: #ffdb6e;
  float: right;
  border: 0;
  border-radius: 14px;
  background: linear-gradient(135deg, #404040, rgba(64, 64, 64, 0) 48.44%);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.25);
}

.contact-form button:hover {
  background-color: #0056b3;
}
@media screen and (max-width: 972px) {
  .contact-form {
    width: 100%;
    height: 100%;
  }
  .contact-form button {
    width: 100%;
    padding-bottom: 10px;
  }
}
